body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 25vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Header {
  position: absolute;
  width: 100%;
  height: auto;
}

.App-logo {
  margin-top: 15px;
  margin-left: 20px;
  height: 60px;
  display: inline-block;
  z-index: 1;
}

.App-logo:hover {
  -webkit-animation-name: App-logo-spin;
          animation-name: App-logo-spin;
  -webkit-animation-duration: 900ms;
          animation-duration: 900ms;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.navigation {
  position: absolute;
  padding-top: 30px;
  right: 10px;
  display: inline-block;
  z-index: 1;
}

.dash-btn {
  height: 40px;
  font-family: Tahoma, Geneva, sans-serif;
  font-weight: 400;
  font-style: normal;
  display: inline-block;
  margin-left: 10px;
  margin-right: 10px;
  color: white;
}

.dash-btn:hover { 
  color: rgba(180, 180, 180, 1);
  cursor: pointer;
}

.dash-btn:focus {
  color: rgba(180, 180, 180, 1);
  outline: none;
  border: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Page {
  width: 100%;
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

.banner-home {
  background-image: url(https//samarelhoussami.github.io/my-website/static/media/bg-home.7f42cda6.jpg);
  background-size:cover;
  width: 100%;
  height: 100vh;
  z-index: -1;
  text-align: center;
}

.banner-contact {
  background-image: url(https//samarelhoussami.github.io/my-website/static/media/bg-contact.9fab4523.jpg);
  background-size:cover;
  width: 100%;
  height: 100vh;
  z-index: -1;
  text-align: center;
}

.banner-about {
  background-image: url(https//samarelhoussami.github.io/my-website/static/media/bg-about.ec26ea62.jpg);
  background-size:cover;
  width: 100%;
  height: 100vh;
  z-index: -1;
  text-align: center;
}

.title {
  display: block;
  padding-top: 40vh;
  color: #fff;
  font-family: "proxima-nova",proxima-nova;
  font-size: 50px;
  font-weight: 400;
}

.subtitle {
  display: inline-block;
  font-family: adobe-garamond-pro;
  font-style: italic;
  font-size: 30px;
  color: #fff;
  font-weight: 400;
}

.page-body {
  background-color: rgb(237, 238, 239);
}

.body-title {
  align-content: center;
  text-align: center;
  padding-top: 30px;
  font-family: "proxima-nova",proxima-nova;
  font-size: 40px;
  font-weight: 400;
  color: rgb(46, 46, 46);
}

.profile-pic {
  display: inline-block;
  height: 20vh;
  float: left;
  margin-right: 10px;
}

.profile-pic:hover {
  cursor: pointer;
}

.bio {
  display: inline-block;
  margin: 30px 50px 30px 50px;
  font-family: "proxima-nova",proxima-nova;
  font-size: 20px;
  font-weight: 400;
  color: rgb(46, 46, 46);
}

.download-btn {
  top: 50px;
  box-sizing: content-box;
  position: relative;
  cursor: pointer;
  padding: 22px 22px;
  border: 4px solid rgb(255,255,255);
  font: normal normal bold 20px/50px Arial, Helvetica, sans-serif;
  color: rgb(255, 255, 255);
  text-align: center;
  text-transform: uppercase;
  text-overflow: clip;
  background: transparent;
  -webkit-transition: 0.1s background-color linear, 0.1s color linear;
}

.download-btn:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(110, 76, 76);
  text-decoration: none;
}

.links {
  margin: 30px auto;
  display: block;
}

.home-link {
  width: 5vw;
  margin: 0 20px 30px 20px;
}

.home-link:hover {
  opacity: 0.8;
}

.arrow-down {
  display: block;
  margin: 35vh auto;
  width: 50px;
  opacity: 0.5;
}

.arrow-down:hover {
  opacity: 1.0;
}

@media screen and (max-width: 1024px) {
	.title { 
		font-size: 50px;
	}

	.subtitle { 
		font-size: 30px;
	}
}

@media screen and (max-width: 950px) {
	.title { 
		font-size: 40px;
	}

	.subtitle { 
		font-size: 20px;
	}
}


@media screen and (max-width: 650px) {
	.title { 
		font-size: 35px;
	}

	.subtitle { 
		font-size: 15px;
	}
}

